<template>
    
    <div>
        <Button icon="bi bi-pencil text-primary" text rounded severity="info" @click="visible = true"/>

        <Dialog v-model:visible="visible" modal @hide="props.onUpdateDone" header="Etkinlik Alanı Güncelle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="update" class="d-flex flex-column h-100">

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Adı</label>
                    <div class="col-lg-6">
                        <InputText class="w-100" v-model="form.name" />
                    </div>
                </div>

                <Divider />

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Adres</label>
                    <div class="col-lg-6">
                        <Textarea autoResize rows="1" cols="30" class="w-100" v-model="form.address" />
                    </div>
                </div>

                <Divider />

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Google Koor.</label>
                    <div class="col-lg-6">
                        <InputText class="w-100" v-model="form.coordinates" />
                    </div>
                </div>

                <div class="separator my-10"></div>
                
                <div>
                    <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                    <button type="button" class="btn btn-light ms-5" @click="cancel()">Vazgeç</button>
                </div>
                
                <FormErrors :form="form"/>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Textarea from "primevue/textarea";
import Divider from 'primevue/divider';
import Button from "primevue/button";

const props = defineProps({
    eventArea:Object,
    onUpdateDone:{
        type:Function,
        required:true
    }
});

const visible = ref(true);

const cancel = () => {
    form.reset();
    props.onUpdateDone();
};

const form = useForm({
    name:props.eventArea.name,
    address:props.eventArea.address,
    coordinates:props.eventArea.coordinates,
});

const update = () => {

    form.put(route('event-areas.update',props.eventArea),{
        preserveScroll: true,
        onSuccess: () => {
            form.reset();
            props.onUpdateDone();
        }
    });
}

</script>